/* eslint-disable */
export function callbacks(state = [], action) {
  if (action.type === 'SET_CALLBACK_ID') {
    let newState = [...state]
    let callbackData = {
      id: action.id,
      data: null
    }
    newState.push(callbackData)
    return newState
  }
  else if (action.type === 'SET_CALLBACK_DATA') {
    let newState = [...state]
    newState.forEach(callback => {
      if (callback.id === action.response.RequestId) {
        callback.data = action.response
      }
    })
    return newState
  }
  else {
    return state
  }
}
