/* eslint-disable */
import React, { Component } from "react"
import { Row, Col, Button, Form, Input, Icon } from "antd"
import { Redirect } from "react-router-dom"
import { connect } from "react-redux"
import { setAuth } from "../actions/auth"
import { setPrevPath } from "../actions/path"
import socket from "../transport/Connect"
import Crypto from "y_crypto"
import helpers from "../helpers"

class Verify extends Component {
  constructor() {
    super()
    this.state = {
      verifyUid: null,
      verifyErr: null,
      verifyRequestSended: false,
      verifyCode: null,
      verifyCodeErr: null,
      btnPreloader: false,
      redirect: false
    }
  }

  componentDidMount() {
    socket.onMessageAuth = data => {
      this.setState({
        btnPreloader: false
      })

      if (data.ErrorCode !== 0) {
        this.setState({
          verifyErr: data.Message
        })
      } else if (data.ResponseType === 10) {
        this.setState({
          verifyRequestSended: true
        })
      } else if (data.ResponseType === 6) {
        localStorage.setItem("FileAccessToken", data.FileAccessToken)
        localStorage.setItem("AccessToken", data.Token.AccessToken)
        localStorage.setItem("RefreshToken", data.Token.RefreshToken)
        localStorage.setItem("User", JSON.stringify(data.User))

        this.props.setAuth("AUTH_SUCCESS")

        this.setState({
          redirect: true
        })
      }
    }


    // шифрование
    const encoder = new TextEncoder()
    const masterPassword = encoder.encode('qwerty')
    let masterPasswordId = BigInt(1)
    const crypto = new Crypto(masterPassword, masterPasswordId)

    const setKeyPair = (keys, keysId) => {
      let allKeyPairs = JSON.parse(localStorage.getItem("keysPair")) || []
      let keysInBase64 = keys
      keysInBase64.id = keysId
      keysInBase64.public = helpers.bufferToBase64(keys.public)
      keysInBase64.private = helpers.bufferToBase64(keys.private)
      allKeyPairs.push(keysInBase64)
  
      localStorage.setItem("keysPair", JSON.stringify(allKeyPairs))
    }

    const setSignKeyPair = (keys, keysId) => {
      let allKeyPairs = JSON.parse(localStorage.getItem("signKeysPair")) || []
      let keysInBase64 = keys
      keysInBase64.id = keysId
      keysInBase64.public = helpers.bufferToBase64(keys.public)
      keysInBase64.private = helpers.bufferToBase64(keys.private)
      allKeyPairs.push(keysInBase64)
  
      localStorage.setItem("signKeysPair", JSON.stringify(allKeyPairs))
    }

    let cryptoKeysId = socket.makeId()

    // создаем ключи шифрования
    crypto.getKeyPairShort(1, cryptoKeysId, 99999, true).then(data => {
      setKeyPair(data, cryptoKeysId)
      cryptoKeysId = socket.makeId()
      crypto.getKeyPairMidle(1, cryptoKeysId, 99999, true).then(data => {
        setKeyPair(data, cryptoKeysId)
        cryptoKeysId = socket.makeId()
        crypto.getKeyPairLong(1, cryptoKeysId, 99999, true).then(data => {
          setKeyPair(data, cryptoKeysId)
        }).catch(err => {console.log(err)})
      }).catch(err => {console.log(err)})
    }).catch(err => {console.log(err)})

    let signKeysId = socket.makeId()

    // создаем ключи подписи
    crypto.getKeyPairShort(1, signKeysId, 99999, false).then(data => {
      setSignKeyPair(data, signKeysId)
      signKeysId = socket.makeId()
      crypto.getKeyPairMidle(1, signKeysId, 99999, false).then(data => {
        setSignKeyPair(data, signKeysId)
        signKeysId = socket.makeId()
        crypto.getKeyPairLong(1, signKeysId, 99999, false).then(data => {
          setSignKeyPair(data, signKeysId)
        }).catch(err => {console.log(err)})
      }).catch(err => {console.log(err)})
    }).catch(err => {console.log(err)})

  }

  onChangePhone(e) {
    this.setState({
      verifyUid: e.target.value,
      verifyErr: null
    })
  }

  onChangeVerifCode(e) {
    this.setState({
      verifyCode: e.target.value,
      verifyCodeErr: null
    })
  }

  getOS() {
    if (navigator.appVersion.indexOf('Windows') >= 0) return 'Windows'
    else if (navigator.appVersion.indexOf('Android') >= 0) return 'Android'
    else if (navigator.appVersion.indexOf('Mac') >= 0) return 'MacOS'
    else return "unknown"
  }

  getDevice() {
    if (navigator.userAgent.indexOf("Android") >= 0) return "Phone"
    else if (navigator.userAgent.indexOf("webOS") >= 0) return "webOS"
    else if (navigator.userAgent.indexOf("iPhone") >= 0) return "iPhone"
    else if (navigator.userAgent.indexOf("iPad") >= 0) return "iPad"
    else if (navigator.userAgent.indexOf("iPod") >= 0) return "iPod"
    else return "PC"
  }

  sendverifyCode(e) {
    e.preventDefault()

    this.setState({
      btnPreloader: true
    })

    const data = {
      Uid: this.state.verifyUid,
      VCode: this.state.verifyCode,
      LoginType: 1,
      UidType: this.props.verifyType === 'by-email' ? 2 : this.props.verifyType === 'by-phone' ? 0 : 0,
      DeviceName: this.getDevice(),
      OSName: this.getOS(),
      AppName: "Y messenger - WEB",
      RequestId: socket.makeId(),
      RequestType: 33,
      Type: 0
    }

    socket.send(data)
  }

  sendVerificationQuery(e) {
    e.preventDefault()

    this.setState({
      btnPreloader: true
    })

    const data = {
      VerificationType: this.props.verifyType === 'by-email' ? 1 : this.props.verifyType === 'by-phone' ? 0 : 0,
      Uid: this.state.verifyUid,
      RequestId: socket.makeId(),
      RequestType: 42,
      Type: 0
    }

    socket.send(data)
  }

  render() {
    if (this.state.redirect) {
      return <Redirect to="/" />
    } else {
      return (
        <Row
          type="flex"
          align="middle"
          justify="center"
          style={{ height: "100vh" }}
        >
          <Col>
            <div className="auth__type">
              <div className="modal_back" onClick={() => this.props.setPrevPath()}><Icon type="arrow-left" /></div>
              <h1>Вход</h1>
              {!this.state.verifyRequestSended ? (
                <div>
                  <h2>
                    {this.props.verifyType === 'by-email' ? 'Введите ваш email' : this.props.verifyType === 'by-phone' ? 'Введите номер телефона' : ''}
                  </h2>
                  <Form
                    onSubmit={this.sendVerificationQuery.bind(this)}
                    className="login-form"
                  >
                    <Form.Item
                      validateStatus={this.state.verifyErr ? "error" : ""}
                      help={this.state.verifyErr ? this.state.verifyErr : ""}
                    >
                      <Input
                        autoFocus
                        inputMode={this.props.verifyType === 'by-email' ? 'email' : this.props.verifyType === 'by-phone' ? 'tel' : ''}
                        placeholder={this.props.verifyType === 'by-email' ? 'Ваш email' : this.props.verifyType === 'by-phone' ? 'Ваш номер телефона' : ''}
                        onChange={this.onChangePhone.bind(this)}
                      />
                    </Form.Item>
                    <Form.Item>
                      <Row>
                        <Col style={{ textAlign: "center" }}>
                          <Button
                            type="primary"
                            htmlType="submit"
                            disabled={!this.state.verifyUid}
                            loading={!!this.state.btnPreloader}
                          >
                            Получить {this.props.verifyType === 'by-email' ? 'письмо' : this.props.verifyType === 'by-phone' ? 'SMS' : ''} с кодом
                          </Button>
                        </Col>
                      </Row>
                    </Form.Item>
                  </Form>
                </div>
              ) : (
                <div>
                  <h2>Код подтверждения из {this.props.verifyType === 'by-email' ? 'письма' : this.props.verifyType === 'by-phone' ? 'SMS' : ''}</h2>
                  <p>
                    Мы отправили {this.props.verifyType === 'by-email' ? 'письмо' : this.props.verifyType === 'by-phone' ? 'SMS' : ''} 
                    на {this.props.verifyType === 'by-email' ? 'email' : this.props.verifyType === 'by-phone' ? 'номер' : ''} {this.state.verifyUid}. Введите
                    полученый код в поле ниже
                  </p>
                  <Form
                    onSubmit={this.sendverifyCode.bind(this)}
                    className="login-form"
                  >
                    <Form.Item
                      validateStatus={this.state.verifyCodeErr ? "error" : ""}
                      help={this.state.verifyCodeErr ? this.state.verifyCodeErr : ""}
                    >
                      <Input
                        autoFocus
                        inputMode='tel'
                        placeholder={`Код из ${this.props.verifyType === 'by-email' ? 'письма' : this.props.verifyType === 'by-phone' ? 'SMS' : ''}, 4 цифры`}
                        onChange={this.onChangeVerifCode.bind(this)}
                      />
                    </Form.Item>
                    <Form.Item>
                      <Row>
                        <Col style={{ textAlign: "center" }}>
                          <Button
                            type="primary"
                            htmlType="submit"
                            disabled={!this.state.verifyCode}
                            loading={!!this.state.btnPreloader}
                          >
                            Войти
                          </Button>
                        </Col>
                      </Row>
                    </Form.Item>
                  </Form>
                </div>
              )}
            </div>
          </Col>
        </Row>
      )
    }
  }
}

function mapStateToProps(state) {
  return {
    verifyType: state.path.currentPath
  }
}

const mapDispatchToProps = dispatch => {
  return {
    setAuth: auth => dispatch(setAuth(auth)),
    setPrevPath: () => dispatch(setPrevPath())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Verify)
