export function setContactUsersIDs(usersIDs) {
  return {
    type: "SET_CONTACT_USERS_IDS",
    usersIDs
  }
}

export function setContacts(contacts) {
  return {
    type: "SET_CONTACTS",
    contacts
  }
}

export function userGroupsReceived(groups) {
  return {
    type: "USER_GROUPS_RECEIVED",
    groups
  }
}

export function removeContactFromGroup(payload) {
  return {
    type: "REMOVE_CONTACT_FROM_GROUP",
    payload
  }
}

export function removeGroup(groupId) {
  return {
    type: "REMOVE_GROUP",
    groupId
  }
}
