/* eslint-disable */
import React, { useState, useEffect } from "react"
import { Modal, Icon } from "antd"
import { connect } from "react-redux"
import { NavLink, Redirect } from "react-router-dom"
import selectors from "../selectors"
import { forwardingMessage, openDialog } from "../actions/dialogs"
import { setCallbackId } from "../actions/callbacks"
import Avatar from "./Avatar"
import Crypto from "y_crypto"
import { setSymmetricKey, setSymmetricKeyForDecrypt } from "../actions/keys"
import helpers from "../helpers"
import socket from "../transport/Connect"
import JSONbig from "json-bigint"
import { ReactComponent as TickIcon } from "../assets/tick_icon.svg"
import { ReactComponent as DoubleTickIcon } from "../assets/double_tick_icon.svg"

function Message(props) {
  const [visible, setVisible] = useState(false)
  const [messageTime, setMessageTime] = useState("")
  const [isContextMenuVisible, setSsContextMenuVisible] = useState(false)
  const [redirect, setRedirect] = useState(false)
  const [decryptedMessage, setDecryptedMessage] = useState(null)
  const [fileMsg, setFileMsg] = useState(false)
  // const [encryptPublicKeyRequest, setEncryptPublicKeyRequest] = useState(null)
  // const [signPublicKeyRequest, setSignPublicKeyRequest] = useState(null)
  const user = selectors.getUserById(props.message.SenderId, props.users) || {}

  const encoder = new TextEncoder()
  const decoder = new TextDecoder()
  const masterPassword = encoder.encode('qwerty')
  let masterPasswordId = BigInt(1)
  const crypto = new Crypto(masterPassword, masterPasswordId)

  useEffect(() => {
    if (props.message) {
      // формируем время сообщения
      const timestamp = new Date(props.message.SendingTime * 1000)
      const date = new Date()
      date.setTime(timestamp)

      let hour = `${date.getHours()}`
      let minutes = `${date.getMinutes()}`

      if (hour.length === 1) {
        hour = `0${hour}`
      }
      if (minutes.length === 1) {
        minutes = `0${minutes}`
      }

      setMessageTime(`${hour}:${minutes}`)
    }
  }, [props.message])

  // useEffect(() => {
  //   console.log(props.publicKeys)
  // }, [props.publicKeys])

  const setLinks = text => {
    let str = text
    let reg = false
    if (str) {
      reg = str.match(/(http|https|ftp|ftps)\:\/\/[a-zA-Z0-9\-\.]+\.[a-zA-Z]{2,3}(\/\S*)?/g)
    }
    let k = 0
    // console.log(reg);
		for (k in reg){
      // console.log(reg[k]);
			str = str.replace(reg[k], ' <a href="' + reg[k] + '" target="_blank"> ' + reg[k] + ' </a> ')
    }

    return str
    
    // let mes_block = this.refs.textMessage;
    // if (mes_block) {
    //   mes_block.innerHTML = str
    // }
  }

  function getForwardingUser(userId) {
    const forwardingUser = props.users.find(user => user.Id === userId)
    if (forwardingUser) {
      return ` ${forwardingUser.NameFirst} ${forwardingUser.NameSecond}`
    }
  }

  function showMessage(message) {
    if (message.Attachments) {
      if (message.Attachments.length) {
        // СООБЩЕНИЯ С ВЛОЖЕНИЯМИ
        // сообщения с изображением
        if (message.Attachments[0].Type === 2) {
          return (
            <div className="message__img" onClick={() => setVisible(true)}>
              <img
                src={`https://${props.node}:5000/api/Files/` + message.Attachments[0].Payload.FileId}
                alt=""
              />
            </div>
          )
        } else if (message.Attachments[0].Type === 1) {
          // сообщение с файлом
          if (message.Attachments[0].Payload) {
            return (
              <div>
                <Icon type="download" className="download-icon" />
                <a href={`https://${props.node}:5000/api/Files/` + message.Attachments[0].Payload.FileId} download>
                  {message.Attachments[0].Payload.Filename}
                </a>
                <span dangerouslySetInnerHTML={{__html: setLinks(message.Text)}}></span>
              </div>
            )
          }
        } else if (message.Attachments[0].Type === 5) {
          // Пересланное сообщение
          if (message.Attachments[0].Payload.length > 0) {
            return (
              <div className="forwarding_message">
                Пересланное сообщение от
                <NavLink to="/" onClick={() => openDialog()}>
                  {getForwardingUser(message.Attachments[0].Payload[0].SenderId)}
                </NavLink>
                <div dangerouslySetInnerHTML={{__html: setLinks(message.Attachments[0].Payload[0].Text)}} className="forward-message-text"></div>
                {
                  message.Text ?
                    <div dangerouslySetInnerHTML={{__html: setLinks(message.Text)}}></div>
                  : null
                }
              </div>
            )
          } else {
            return (
              <div className="forwarding_message">
                Пересланное сообщение от:
                <div>Пересланное сообщение удалено</div>
              </div>
            )
          }
        } else if (message.Attachments[0].Type === 6) {
          // console.log(message)
            // let foundPublicKey = null
            let foundPublicSignKey = null
            if (message.SenderId === props.user.Id) {
              // console.log('out - ', JSONbig.stringify(message.Attachments[0].Payload.SignKeyId))
            }
            else {
              // console.log('into - ', message)
              // foundPublicKey = props.publicKeys.find(publicKey => JSONbig.stringify(publicKey.KeyId) === JSONbig.stringify(message.Attachments[0].Payload.KeyId))
              foundPublicSignKey = props.publicKeys.find(publicKey => JSONbig.stringify(publicKey.KeyId) === JSONbig.stringify(message.Attachments[0].Payload.SignKeyId))

              if (!foundPublicSignKey) {
                // console.log("запршиваем ключ подписи")
                let requestId2 = socket.makeId()
                // localStorage.setItem("encryptPublicKey", requestId2)
                const getUserKeys2 = {
                  UserId: message.SenderId,
                  Direction: true,
                  KeysId: [JSONbig.stringify(message.Attachments[0].Payload.SignKeyId)],
                  RequestType: 105,
                  RequestId: requestId2,
                  Type: 0
                }
                socket.send(getUserKeys2)
              }
            }
          
            if(foundPublicSignKey){
              let keysPair = JSON.parse(localStorage.getItem("keysPair"))
              let privateKey = keysPair.find(key => JSONbig.stringify(key.id) === JSONbig.stringify(message.Attachments[0].Payload.KeyId))
              if (privateKey) {
                crypto.setPublicSignKeyToRecive(helpers.base64ToBuffer(foundPublicSignKey.Data))
                crypto.setPrivateEncryptKeyToRecive(helpers.base64ToBuffer(privateKey.private))
  
                crypto.decryptKeyMsg(helpers.base64ToBuffer(message.Attachments[0].Payload.EncryptedData)).then(data => {
                  const symmetricKeyForDecrypt = props.symmetricKeysForDecrypt.find(key => key.idKey == data.idKey)
                  // console.log('симметричный расшифрован - ', data)
                  if (!symmetricKeyForDecrypt) {
                    props.setSymmetricKeyForDecrypt(data)
                  }
                }).catch(err => {
                  console.log(err)
                })
              } else {
                // console.log("Не найден приватный ключ")
              }
            }


            
            // if (message.Attachments[0].Payload.KeyId.toString().length < 15) {
            // props.setSymmetricKeyForDecrypt(payload)
            // }
            // if (!props.symmetricKey) {
              // props.setSymmetricKey(message.Attachments[0].Payload.EncryptedData)
              // localStorage.setItem('keyID', message.Attachments[0].Payload.KeyId)
            // }
          return <div>Симметричный ключ шифрования</div>
        } else if (message.Attachments[0].Type === 4) {
          // console.log(message)
          const symmetricKeyForDecrypt = props.symmetricKeysForDecrypt.find(key => key.idKey == JSONbig.stringify(message.Attachments[0].Payload.KeyId))
          let failedDecryptMsg = false
          // console.log(message.GlobalId, symmetricKeyForDecrypt)
          if (symmetricKeyForDecrypt) {
            const signKeyForDecrypt = props.publicKeys.find(key => JSONbig.stringify(key.KeyId) == JSONbig.stringify(message.Attachments[0].Payload.SignKeyId))
            if (signKeyForDecrypt) {
              // console.log(message)
              // console.log(symmetricKeyForDecrypt)
              // console.log(signKeyForDecrypt)
              crypto.setSymmetricKey(symmetricKeyForDecrypt.key)
              crypto.setPublicSignKeyToRecive(helpers.base64ToBuffer(signKeyForDecrypt.Data))
              crypto.decryptSecretMsg(helpers.base64ToBuffer(message.Attachments[0].Payload.EncryptedData)).then(result => {
                if (Crypto.getMetaDataMsg(helpers.base64ToBuffer(message.Attachments[0].Payload.EncryptedData)).attach.type === 0) {
                  setDecryptedMessage(decoder.decode(result.msg))
                } else {
                  setFileMsg(true)
                  setDecryptedMessage("Просмотр файлов доступен только в мобильной версии приложения")
                }
                // console.log(message)
              }).catch(err => {
                console.log(err)
                setDecryptedMessage("Неподдерживаемое зашифрованное сообщение")
              })
            }
            // console.log(symmetricKeyForDecrypt)
            // const encoder2 = new TextEncoder()
            // const decoder2 = new TextDecoder()
            // const masterPassword2 = encoder.encode('qwerty')
            // let masterPasswordId2 = BigInt(1)
            // const crypto2 = new Crypto(masterPassword2, masterPasswordId2)
          } else {
            // console.log("Не удалось расшифровать сообщение")
            failedDecryptMsg = "Зашифрованное сообщение"
            // setDecryptedMessage("Не удалось расшифровать сообщение")
          }
          
          return (
            <div style={!decryptedMessage || fileMsg ? {fontStyle: "italic"} : null}>
              {failedDecryptMsg ? failedDecryptMsg : decryptedMessage ? decryptedMessage : "Сообщение расшифровывается..."}
            </div>
          )
        } else {
          // прочие типы сообщений
          return <div style={{fontStyle: "italic"}}>*неподдерживаемый тип сообщения*</div>
        }
      } else {
        // обычное сообщение с текстом
        return message.Text ? (
          <div dangerouslySetInnerHTML={{__html: setLinks(message.Text)}}></div>
        ) : null
      }
    } else {
      // обычное сообщение с текстом
      return message.Text ? (
        <div>{message.Text}</div>
      ) : null
    }
  }

  function showMessageMenu() {
    setSsContextMenuVisible(!isContextMenuVisible)
  }

  const handleReplyToMessage = () => {
    props.forwardingMessage(props.message)
  }

  const handleForwardMessage = () => {
    props.forwardingMessage(props.message)
    setRedirect(true)
  }

  const openDialog = () => {
    const conversation = props.dialogs.find(dialog => dialog.ConversationId === props.message.Attachments[0].Payload[0].ConversationId && dialog.ConversationType === props.message.Attachments[0].Payload[0].ConversationType)
    if (conversation) {
      const payload = {}
      payload.type = "dialog"
      payload.data = conversation
      props.openDialog(payload)
    }
  }

  if (redirect) {
    return <Redirect to="/" />
  }
  if (user && props.message) {
    return (
      <div
        className={
          user.currentUser
            ? "message message-out"
            : "message message-in"
        }
        id={props.message.GlobalId}
        onClick={showMessageMenu}
        style={props.message && isContextMenuVisible ? { background: "#ebf5ff" } : null }
      >
        <div className="wrap-message-content">
          {!user.currentUser && props.message.ConversationType !== 3 ? <Avatar user={user} /> : null}
          {
            user.currentUser && props.message.Read ?
              <DoubleTickIcon className='message__read' />
            : user.currentUser && !props.message.Read ? 
              <TickIcon className='message__read' />
            : null
          }
          <div 
            className={
              props.message.Attachments[0] ?
                props.message.Attachments[0].Type === 6 ? "message__content key_message"  :
                props.message.Attachments[0].Type === 4 ? "message__content encrypted_message"  : null
              : "message__content"
            }
          >
            {
              !user.currentUser ?
                <div className="message__name">
                  {user.NameFirst ? user.NameFirst + ' ' : null}
                  {user.NameSecond ? user.NameSecond : null}
                </div>
              : null
            }          
            {showMessage(props.message)}
          </div>
          <div className="message__time">{messageTime}</div>
        </div>
        {props.message.Attachments && props.message.Attachments[0] ? (
          <Modal
            visible={visible}
            onCancel={() => setVisible(false)}
            footer={null}
            maskClosable
          >
            <img
              className="popup__img"
              src={`https://${props.node}:5000/api/Files/${props.message.Attachments[0].Payload.FileId}`}
              alt=""
            />
          </Modal>
        ) : null}
        {props.message && isContextMenuVisible ? (
          <div className="message-menu">
            <ul>
              <li onClick={handleReplyToMessage}>Ответить</li>
              <li onClick={handleForwardMessage}>Переслать</li>
            </ul>
          </div>
        ) : null}
      </div>
    )
  } else {
    return null
  }
}

function mapStateToProps(state) {
  return {
    users: state.users,
    user: state.users.find(user => user.currentUser === true),
    dialogs: state.dialogs,
    forwardMessage: state.forwardMessage,
    node: state.node.activeNode.Domains[0],
    // symmetricKey: state.symmetricKey,
    symmetricKeysForDecrypt: state.symmetricKeysForDecrypt,
    // callbacks: state.callbacks,
    publicKeys: state.publicKeys
  }
}

const mapDispatchToProps = dispatch => {
  return {
    forwardingMessage: message => dispatch(forwardingMessage(message)),
    openDialog: dialogId => dispatch(openDialog(dialogId)),
    setSymmetricKey: key => dispatch(setSymmetricKey(key)),
    setSymmetricKeyForDecrypt: key => dispatch(setSymmetricKeyForDecrypt(key)),
    setCallbackId: id => dispatch(setCallbackId(id))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Message)
