/* eslint-disable */
import React, {useState} from "react"
import { connect } from "react-redux"
import { Modal, Button, Input, Divider, Form, Typography, Icon } from "antd"
import { toggleModal } from "../actions/ui"
import { removeContactFromGroup, removeGroup } from "../actions/contacts"
import Avatar from "../components/Avatar"
import socket from "../transport/Connect"

const { Text } = Typography

function ContactsGroupsModal(props) {
  const [chatName, setChatName] = useState(null)
  const [nameError, setNameError] = useState(false)
  const [groupOpen, setGroupOpen] = useState(false)
  const [creatingGroup, setCreatingGroup] = useState(false)
  const [usersIDs, setUsersIds] = useState([])
  const [showUsers, setShowUsers] = useState(false)

  const handleSetChatName = value => {
    setChatName(value)
    setNameError(false)
    if (value.length < 1) {
      setNameError(true)
    }
  }

  const removeContactGroup = (groupId) => {
    const removeGroup = {
      GroupsId: [groupId],
      RequestId: socket.makeId(),
      RequestType: 57,
      Type: 0
    }

    socket.send(removeGroup)
    props.removeGroup(groupId)
  }

  const showGroups = () => {
    return props.groups.map(group => {
      return (
        <div className="contact" key={group.GroupId}>
          <div className="name" onClick={() => openGroup(group.GroupId)}>{group.Title}</div>
          <Button className="remove-user" type="danger" shape="circle" icon="close" onClick={() => removeContactGroup(group.GroupId)} />
        </div>
      )
    })
  }

  const openGroup = groupId => {
    const openedGroup = props.groups.find(group => group.GroupId === groupId)
    if (openedGroup) {
      setGroupOpen(openedGroup)
    }
  }

  const handleSetCreatingGroup = () => {
    if (!chatName) {
      setNameError(true)
    } else {
      setCreatingGroup("step2")
    }
  }

  const updateGroup = () => {
    const addUsersToGroup = {
      UsersId: usersIDs,
      GroupId: groupOpen.GroupId,
      RequestId: socket.makeId(),
      RequestType: 58,
      Type: 0
    }
    
    socket.send(addUsersToGroup)

    props.toggleModal("contactsGroupsModal")
  }

  const createGroup = () => {
    const newGroup = {
      Group: {
        GroupId: groupOpen ? groupOpen.GroupId : null,
        Title: chatName ? chatName : groupOpen.Title,
        UsersId: usersIDs,
      },
      RequestId: socket.makeId(),
      RequestType: 56,
      Type: 0
    }
    
    socket.send(newGroup)

    props.toggleModal("contactsGroupsModal")
  }

  const removeUserFromGroup = user => {
    const removeUser = {
      UsersId: [user.Id],
      GroupId: groupOpen.GroupId,
      RequestId: socket.makeId(),
      RequestType: 59,
      Type: 0
    }
    
    socket.send(removeUser)

    props.removeContactFromGroup({userId: user.Id, groupId: groupOpen.GroupId})
  }

  const showUsersInGroup = () => {
    return props.contacts.users.map(user => {
      if(user.GroupsId.includes(groupOpen.GroupId)){
        let name = null
        if (user.ContactUser.NameFirst) {
          name = user.ContactUser.NameFirst
        }
        if (user.ContactUser.NameSecond) {
          name = name + ' ' + user.ContactUser.NameSecond
        }
        return (
          <div key={user.ContactId} className='contacts'>
            <>
            <Avatar user={user.ContactUser} />
            {name ? name : 'Имя скрыто'}
            </>
            <Button className="remove-user" type="danger" shape="circle" icon="user-delete" onClick={() => removeUserFromGroup(user.ContactUser)} />
          </div>
        )
      }
    })
  }

  const addContactToChat = userId => {
    let users = [...usersIDs]
    if (usersIDs.includes(userId)) {
      let foudnUserIndex = users.indexOf(userId)
      users.splice(foudnUserIndex, 1)
      setUsersIds(users)
    } else {
      users.push(userId)
      setUsersIds(users)
    }
  }

  const showContacts = () => {
    let contacts = []
    if (groupOpen) {
      contacts = props.contacts.users.map(contact => {
        if (!contact.GroupsId.includes(groupOpen.GroupId)) {
          return contact
        }
      })
    } else {
      contacts = props.contacts.users
    }
    return contacts.map(user => {
      if(user){
        let name = null
        if (user.ContactUser.NameFirst) {
          name = user.ContactUser.NameFirst
        }
        if (user.ContactUser.NameSecond) {
          name = name + ' ' + user.ContactUser.NameSecond
        }
        return (
          <div key={user.ContactId} className={usersIDs.includes(user.ContactUserId) ? 'contacts contacts-active' : 'contacts'} onClick={() => {addContactToChat(user.ContactUserId)}}>
            <Avatar user={user.ContactUser} />
            {name ? name : 'Имя скрыто'}
          </div>
        )
      }
    })
  }

  const handleCloseModal = () => {
    setChatName(null)
    setNameError(false)
    setGroupOpen(false)
    setCreatingGroup(false)
    setUsersIds([])
    setShowUsers(false)
    props.toggleModal("contactsGroupsModal")
  }

  return (
    <Modal
      visible={props.contactsGroupsModal}
      // visible={true}
      onCancel={() => handleCloseModal()}
      title="Группы контактов"
      footer={null}
      maskClosable
      className="contacts create-chat-modal"
    >
      {
        creatingGroup === "step1" ?
          <div className="content">
            <div className="modal_back" onClick={() => setCreatingGroup(false)}><Icon type="arrow-left" /></div>
            <Form.Item
              validateStatus={nameError ? "error" : ''}
              help={nameError ? "Это поле обязательно для заполнения" : false}
            >
              <Input
                placeholder="Название группы"
                onChange={(e) => handleSetChatName(e.target.value)}
              />
            </Form.Item>
            <Divider />
            <Button type="primary" block onClick={() => handleSetCreatingGroup()}>Далее</Button>
          </div>
        : creatingGroup === "step2" ?
          <div className="content">
            <div className="modal_back" onClick={() => setCreatingGroup("step1")}><Icon type="arrow-left" /></div>
            <Text strong>Добавьте участников</Text>
            <div className="wrap_contacts">
              {showContacts()}
            </div>
            <Divider />
            <Button type="primary" block onClick={() => createGroup()}>Создать группу</Button>
          </div>
        : groupOpen ?
          <div className="content">
            {
              showUsers ?
                <>
                  <div className="modal_back" onClick={() => setShowUsers(false)}><Icon type="arrow-left" /></div>
                  <Text strong>Добавьте участников</Text>
                  <div className="wrap_contacts">
                    {showContacts()}
                  </div>
                  <Divider />
                </>
              :
                <>
                  <div className="modal_back" onClick={() => setGroupOpen(false)}><Icon type="arrow-left" /></div>
                  <Text strong>Название:</Text>
                  <Input defaultValue={chatName ? chatName : groupOpen.Title} onChange={(e) => handleSetChatName(e.target.value)} />
                  <Divider />
                  <Text strong>Участники:</Text>
                  <div className="wrap_contacts">
                    {showUsersInGroup()}
                  </div>
                  <Divider />
                  <Button type="normal" block icon="usergroup-add" onClick={() => setShowUsers(!showUsers)}>Добавить пользователей</Button>
                  <Divider />
                </>
            }
            <Button type="primary" block onClick={() => updateGroup()}>Сохранить</Button>
          </div>
        :
          <>
            {showGroups()}
            <div className="content">
              <Button type="primary" block onClick={() => setCreatingGroup("step1")}>Добавить группу</Button>
            </div>
          </>
      }
    </Modal>
  )
}

function mapStateToProps(state) {
  return {
    contactsGroupsModal: state.ui.modals.contactsGroupsModal,
    users: state.users,
    contacts: state.contacts,
    contactsIDs: state.contacts.usersIDs,
    groups: state.contacts.groups
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleModal: name => dispatch(toggleModal(name)),
    removeContactFromGroup: data => dispatch(removeContactFromGroup(data)),
    removeGroup: data => dispatch(removeGroup(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ContactsGroupsModal)