import JSONbig from "json-bigint"
// const defaultState = {
//   symmetricKey: null,
//   keysPair: null
// }

export function publicKeys(state = [], action) {
  if(action.type === 'SET_PUBLIC_KEYS') {
    let newState = [...state]

    action.publicKeys.forEach(publicKey => {
      let foundPublicKey = newState.find(key => JSONbig.stringify(key.KeyId) === JSONbig.stringify(publicKey.KeyId))
      if (!foundPublicKey) {
        newState.push(publicKey)
      }
    })

    return newState
  } else {
    return state
  }
}
