/* eslint-disable */
import React, { useState, useEffect } from "react"
import { Row, Col, Button, Form, Input, Dropdown, Menu, Icon } from "antd"
import { connect } from "react-redux"
import socket from "../transport/Connect"

function Registration(props) {
  const [name, setName] = useState("")
  const [surname, setSurname] = useState("")
  const [phone, setPhone] = useState("")
  const [email, setEmail] = useState("")
  const [verifyCode, setVerifyCode] = useState("")
  const [nameErr, setNameErr] = useState(false)
  const [emailErr, setEmailErr] = useState(false)
  const [contactsErr, setContactsErr] = useState(false)
  const [verifyCodeErr, setVerifyCodeErr] = useState(false)
  const [btnLoader, setbtnLoader] = useState(false)
  const [activeNode, setActiveNode] = useState(null)
  const [showRegButton, setShowRegButton] = useState(false)

  useEffect(() => {
    socket.onMessage = data => {
      if (data.ErrorCode !== 0) {
        // console.log(data)
      } else if (data.ResponseType === 6) {
        localStorage.setItem("FileAccessToken", data.FileAccessToken)
        localStorage.setItem("AccessToken", data.Token.AccessToken)
        localStorage.setItem("RefreshToken", data.Token.RefreshToken)
        localStorage.setItem("User", JSON.stringify(data.User))

        window.location.replace("/")
      }
    }

    setActiveNode(JSON.parse(localStorage.getItem("node")))
  }, [])

  const handleRegistration = () => {
    if (!verifyCode) {
      setVerifyCodeErr("Это поле обязательно для заполнения")
    } else {
      setbtnLoader(true)

      const regData = {
        User: {
          Id: 0,
          Phones: phone ? [phone] : null,
          NameFirst: name,
          NameSecond: surname,
          About: "",
          Photo: "",
          Country: "",
          City: "",
          Birthday: "",
          Language: null,
          NodeId: activeNode.Id,
          Online: null,
          Emails: email ? [email] : null,
          Blacklist: null,
          Visible: null,
          Security: null,
          Tag: 0,
          RegistrationDate: null
        },
        VCode: parseInt(verifyCode),
        RequestId: socket.makeId(),
        RequestType: 37,
        Type: 0
      }

      socket.send(regData)
    }
  }

  const handleSetName = e => {
    setName(e.target.value)
    setNameErr(false)
  }

  const handleSetSurname = e => {
    setSurname(e.target.value)
  }

  const handleSetPhone = e => {
    setPhone(e.target.value)
    setContactsErr(false)
  }

  const handleSetEmail = e => {
    const validateEmail = email => {
      var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    }

    setEmail(e.target.value)
    setContactsErr(false)

    if (!validateEmail(e.target.value)) {
      setEmailErr("Некорректный email адрес")
    } else {
      setEmailErr(false)
    }

    if (e.target.value.length === 0) {
      setEmailErr(false)
    }
  }

  const handleSetVerifyCode = e => {
    setVerifyCode(e.target.value)
    setVerifyCodeErr(false)
  }

  const selectNode = data => {
    if (data.item.props.data) {
      setActiveNode(data.item.props.data)
      localStorage.setItem("node", JSON.stringify(data.item.props.data))
    }
  }

  const menu = (
    <Menu onClick={(node) => selectNode(node)}>
      {props.nodes.map((node, i) => {
        return (
          <Menu.Item key={i} data={node}>
            {node.Name}
          </Menu.Item>
        )
      })}
      {
        // this.state.activeNode ? <Menu.Item key='auto' data={null}>Автоматически</Menu.Item> : null
      }
    </Menu>
  )

  const getVerifyCode = () => {
    if (name.length === 0) {
      setNameErr("Это поле обязательно для заполнения")
    } else if (phone.length === 0 && email.length === 0) {
      setContactsErr("Одно из этих полей обязательно для заполнения")
    } else {
      const verifyData = {
        VerificationType: email ? 1 : phone ? 0 : 0,
        Uid: phone ? phone : email,
        IsRegistration: true,
        RequestId: socket.makeId(),
        RequestType: 42,
        Type: 0
      }

      setShowRegButton(true)

      socket.send(verifyData)
    }
  }

  return (
    <Row
      type="flex"
      align="middle"
      justify="center"
      style={{ height: "100vh" }}
    >
      <Col>
        <div className="auth__type">
          <h1>Регистрация</h1>
          <div>
            <Form className="login-form">
              {
                showRegButton ? 
                  <>
                    <div className="verify-text">{`Мы отправили вам код подтверждения на ваш ${phone ? 'телефон' : 'email'}, пожалуйста введите его в поле ниже и завершите регистрацию`}</div>
                    <Form.Item
                      validateStatus={verifyCodeErr ? "error" : ""}
                      help={verifyCodeErr || ""}
                    >
                      <Input placeholder="Код" value={verifyCode} onChange={handleSetVerifyCode} />
                    </Form.Item>
                    <Form.Item>
                      <Row>
                        <Col style={{ textAlign: "center" }}>
                          <Button
                            type="primary"
                            onClick={handleRegistration}
                            loading={btnLoader}
                          >
                            Зарегистрироваться
                          </Button>
                        </Col>
                      </Row>
                    </Form.Item>
                  </>
                :
                  <>
                    <Form.Item
                      validateStatus={nameErr ? "error" : ""}
                      help={nameErr || ""}
                    >
                      <Input placeholder="Имя" onChange={handleSetName} />
                    </Form.Item>
                    <Form.Item validateStatus="">
                      <Input placeholder="Фамилия" onChange={handleSetSurname} />
                    </Form.Item>
                    <Form.Item
                      validateStatus={contactsErr ? "error" : ""}
                      help={contactsErr || ""}
                    >
                      <Input placeholder="Номер телефона" disabled={email.length ? true : false} onChange={handleSetPhone} />
                    </Form.Item>
                    <div className="verify-text"><span>или</span></div>
                    <Form.Item
                      validateStatus={contactsErr || emailErr ? "error" : ""}
                      help={contactsErr || emailErr || ""}
                    >
                      <Input placeholder="Email" disabled={phone.length ? true : false} onChange={handleSetEmail} />
                    </Form.Item>
                    <Form.Item>
                      <Row>
                        <Col style={{ textAlign: "center" }}>
                          <Button
                            type="primary"
                            onClick={() => getVerifyCode()}
                            disabled={name && phone || email ? false : true}
                          >
                            Получить код
                          </Button>
                        </Col>
                      </Row>
                    </Form.Item>
                  </>
              }
            </Form>
            <div className="auth__type_server">
              <span>Сервер: </span>
              <Dropdown overlay={menu}>
                <span className="ant-dropdown-link">
                  {activeNode
                    ? activeNode.Name
                    : "Автоматически"}
                  <Icon type="down" />
                </span>
              </Dropdown>
            </div>
          </div>
        </div>
      </Col>
    </Row>
  )
}

function mapStateToProps(state) {
  return {
    nodes: state.node.allNodes
  }
}

const mapDispatchToProps = dispatch => {
  return {}
}

export default connect(mapStateToProps, mapDispatchToProps)(Registration)