export function setSymmetricKey(symmetricKey) {
  return {
    type: "SET_SYMMETRIC_KEY",
    symmetricKey
  }
}

export function setSymmetricKeyForDecrypt(symmetricKeyForDecrypt) {
  return {
    type: "SET_SYMMETRIC_KEY_FOR_DECRYPT",
    symmetricKeyForDecrypt
  }
}

export function setPublicKeys(publicKeys) {
  return {
    type: "SET_PUBLIC_KEYS",
    publicKeys
  }
}
