/* eslint-disable */
import React, {useState, useEffect} from "react"
import { connect } from "react-redux"
import { Modal, Button, Input, Divider, Upload, Icon, message, Switch } from "antd"
import { toggleModal } from "../actions/ui"
import Request from "../transport/Request"
import Avatar from "./Avatar"
import axios from 'axios'

function SettingsModal(props) {
  const [loading, setLoading] = useState(false)
  const [imageUrl, setImageUrl] = useState(false)
  const [name, setName] = useState('')
  const [lastName, setLastName] = useState('')
  const [about, setAbout] = useState('')
  const [privacySettings, setPrivacySettings] = useState(false)
  const [namePrivacy, setNamePrivacy] = useState(false)
  const [photoPrivacy, setPhotoPrivacy] = useState(false)
  const [onlinePrivacy, setOnlinePrivacy] = useState(false)
  const [phonePrivacy, setPhonePrivacy] = useState(false)
  const [emailPrivacy, setEmailPrivacy] = useState(false)

  useEffect(() => {
    if (props.user) {
      setName(props.user.NameFirst)
      setLastName(props.user.NameSecond)
      setAbout(props.user.About)
      setImageUrl(props.user.Photo)
      setNamePrivacy(props.user.Visible[0][1])
      setPhotoPrivacy(props.user.Visible[0][2])
      setOnlinePrivacy(props.user.Visible[0][14])
      setPhonePrivacy(props.user.Visible[0][15])
      setEmailPrivacy(props.user.Visible[0][17])
    }
  }, [props.user])
  
  const beforeUpload = file => {
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png'
    if (!isJpgOrPng) {
      message.error('Поддерживается только загрузка JPG/PNG файлов!')
    }
    return isJpgOrPng
  }

  const handleChange = info => {
    if (info.file.status === 'uploading') {
      setLoading(true)
      return
    }
  }

  const uploadButton = (
    <div>
      <Icon type={loading ? 'loading' : 'plus'} />
      <div className="ant-upload-text">Upload</div>
    </div>
  )

  const handleUploadImage = data => {
    const fileAccessToken = localStorage.getItem('FileAccessToken')
    axios.defaults.headers['FileAccessToken'] = fileAccessToken

    const formData = new FormData()
    formData.append('file', data.file)
  
    axios.post(`https://${props.node}:5000/api/Files`, formData)
    .then(
        (res) => {
          if (res.status === 200) {
            setLoading(false)
            setImageUrl(res.data.File.FileId)
          }
        }
    )
    .catch(
        (err) => {
          console.log(err)
        }
    )
  }

  const handleUpdateUser = () => {
    let privacy = props.user.Visible[0]
    privacy[1] = namePrivacy
    privacy[2] = photoPrivacy
    privacy[14] = onlinePrivacy
    privacy[15] = phonePrivacy
    privacy[17] = emailPrivacy

    const userData = {
      NameFirst: name,
      NameSecond: lastName,
      About: about,
      Photo: imageUrl,
      Privacy: privacy
    }

    Request.editUser(userData)
  }

  return (
    <Modal
      visible={props.settingsModal}
      // visible={true}
      onCancel={() => props.toggleModal("settingsModal")}
      title="Редактировать профиль"
      footer={null}
      maskClosable
      className="contacts"
    >
      {
        props.user ?
          <div className="content settings">
            {!privacySettings ?
              <>
                <Upload
                  listType="picture-card"
                  className="avatar-uploader"
                  showUploadList={false}
                  customRequest={file => handleUploadImage(file)}
                  beforeUpload={beforeUpload}
                  onChange={handleChange}
                >
                  {
                    loading ? 
                      <Icon type="loading" className="loading-icon" />
                    : null
                  }
                  {
                    imageUrl ?
                      <img src={`https://${props.node}:5000/api/Files/${imageUrl}`} alt="avatar" style={{ width: '100%' }} />
                    : props.user.Photo ?
                      <Avatar user={props.user} />
                    : uploadButton
                  }
                </Upload>
                <Divider />
                <Input value={lastName} onChange={e => setLastName(e.target.value)} placeholder="Фамилия" />
                <Divider />
                <Input value={name} onChange={e => setName(e.target.value)} placeholder="Имя" />
                <Divider />
                <Input value={about} onChange={e => setAbout(e.target.value)} placeholder="О себе" />
                <Divider />
                <Input addonBefore="+7" defaultValue={props.user.Phones[0] ? props.user.Phones[0].SubscriberNumber : ''} placeholder="Телефон" disabled />
                <Divider />
                <Input defaultValue={props.user.Emails[0]} placeholder="Email" disabled />
                <Divider />
                <Button type="link" onClick={() => {setPrivacySettings(true)}}>Настроки приватности</Button>
              </>
            :
              <>
                <div className="modal_back" onClick={() => setPrivacySettings(false)}><Icon type="arrow-left" /></div>
                <div className='switch-label'>
                  <div>Имя, тэг</div>
                  <Switch checked={namePrivacy} onChange={(value) => setNamePrivacy(value)} />
                </div>
                <Divider />
                <div className='switch-label'>
                  <div>Онлайн статус</div>
                  <Switch checked={onlinePrivacy} onChange={(value) => setOnlinePrivacy(value)} />
                </div>
                <Divider />
                <div className='switch-label'>
                  <div>Телефон</div>
                  <Switch checked={phonePrivacy} onChange={(value) => setPhonePrivacy(value)} />
                </div>
                <Divider />
                <div className='switch-label'>
                  <div>Email</div>
                  <Switch checked={emailPrivacy} onChange={(value) => setEmailPrivacy(value)} />
                </div>
                <Divider />
                <div className='switch-label'>
                  <div>Фото, о себе</div>
                  <Switch checked={photoPrivacy} onChange={(value) => setPhotoPrivacy(value)} />
                </div>
              </>
            }
            <Divider />
            <Button type="primary" block onClick={() => handleUpdateUser()}>Сохранить</Button>
          </div>
        : null
      }        
    </Modal>
  )
}

function mapStateToProps(state) {
  return {
    settingsModal: state.ui.modals.settingsModal,
    user: state.users.find(user => user.currentUser === true),
    node: state.node.activeNode.Domains[0]
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleModal: name => dispatch(toggleModal(name))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SettingsModal)