/* eslint-disable */
const defaultState = {
  users: [],
  usersIDs: [],
  groups: []
}

export function contacts(state = defaultState, action) {
  if(action.type === 'SET_CONTACT_USERS_IDS') {
    let newState = {...state}
    newState.usersIDs = action.usersIDs
    return newState
  }
  if(action.type === 'SET_CONTACTS') {
    let newState = {...state}
    newState.users = action.contacts
    return newState
  }
  else if (action.type === "USER_GROUPS_RECEIVED") {
    let newState = {...state}
    newState.groups = action.groups
    return newState
  }
  else if (action.type === "REMOVE_CONTACT_FROM_GROUP") {
    let newState = {...state}
    newState.users.forEach(user => {
      if (user.ContactUserId === action.payload.userId) {
        let groupIndex = user.GroupsId.indexOf(action.payload.groupId)
        user.GroupsId.splice(groupIndex, 1)
      }
    })
    return newState
  }
  else if (action.type === "REMOVE_GROUP") {
    let newState = {...state}
    newState.groups.forEach(group => {
      if (group.GroupId === action.groupId) {
        let groupIndex = newState.groups.indexOf(group)
        newState.groups.splice(groupIndex, 1)
      }
    })
    return newState
  }
  else {
    return state
  }
}
