export function symmetricKeysForDecrypt(state = [], action) {
  if(action.type === 'SET_SYMMETRIC_KEY_FOR_DECRYPT') {
    let newState = [...state]
    let keyAdded = newState.find(key => key.KeyId === action.symmetricKeyForDecrypt.KeyId)
    if (!keyAdded) {
      newState.push(action.symmetricKeyForDecrypt)
    }

    return newState
  } else {
    return state
  }
}
